    await getCandidates(data.value.currentPath, data.value.currentPage);
  }
}

async function nextPage() {
  if (data.value.hasNext) {
    data.value.currentPage += 1;
    await getCandidates(data.value.currentPath, data.value.currentPage);
  }
}

