      'Authorized': ['alert-success', 'Вы успешно авторизованы']
    };
    Object.assign(data.value, {
      attr: alerts[user][0],
      text: alerts[user][1],
    })
    if (user === "Success"){
      Object.assign(data.value, {
        path: '/login',
        title: 'Вход в систему',
        value: 'Войти'
      })
    } else if (user === "Authorized"){
      localStorage.setItem('jwt_token', access_token);
